import { React, PureComponent } from 'react';
import '../assets/login.scss';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { URL_LOGIN, getUsers, createUser, updateUser } from '../api/user';
import Home from './Home';
import Signin from './Signin';
import openprofLogo from '../assets/openprof.png';


class Login extends PureComponent {
     constructor() {
          super();
          this.state = {
               emailCorrect: false,
               passwordCorrect: false,
               connexionToast: false,
               users: null,
               isRedirected: false,
               accountUserIfValid: [],
               wantToSignIn: false,
          }
          this.emailListener = this.emailListener.bind(this)
          this.passwordListener = this.passwordListener.bind(this)
          this.formLogin = [
               { email: null, password: null }
          ]
          this.verifyConnexion = this.verifyConnexion.bind(this);
          this.onSubmit = this.onSubmit.bind(this);
          this.connection = this.connection.bind(this);
     }
     componentDidMount = () => {
          getUsers().then((res) => {
               this.setState({ users: res })
               console.log("RES --> ", res)
          }).catch((error) => { console.log(error) })
     }

     emailListener = e => {
          console.log(e.target.value)
          if (e.target.value === "" || e.target.value === null) {
               this.setState({
                    emailCorrect: false
               })

          } else {
               this.setState({
                    emailCorrect: true
               })
          }
          this.formLogin.email = e.target.value
     }

     passwordListener = e => {
          if (e.target.value.length <= 7) {
               this.setState({
                    passwordCorrect: false
               })
          } else {
               this.setState({
                    passwordCorrect: true
               })
          }
          this.formLogin.password = e.target.value
     }

     onSubmit = e => {
          const { users } = this.state
          e.preventDefault()
          const __DATA__ = {
               email: this.formLogin && this.formLogin.email,
               password: this.formLogin && this.formLogin.password
          }
          this.setState({
               connexionToast: (<ToastContainer />)
          })
          var verification = this.verifyConnexion(users)
          if (verification) {
               console.log("Okkkkk")
               this.setState({ isRedirected: true, accountUserIfValid: [this.formLogin.email, this.formLogin.password] })
               toast.success("Connexion réussie")
          } else {
               toast.warn("Aucun compte trouvé avec l'email " + this.formLogin.email)
               this.setState({ isRedirected: false })
          }
          this.connection(this.formLogin.email, {"pseudo": this.formLogin.email, "etat": "En ligne"})

     }

     verifyConnexion(users) {
          var canConnect = false
          getUsers().then((res) => { this.setState({ users: res }) }).catch((error) => { console.log(error) })
          users.map(user => {
               if ((user.email == this.formLogin.email) && (user.password == this.formLogin.password)) {
                    canConnect = true
               }
          })
          return canConnect
     }

     createAccount = e =>{
          e.preventDefault();
     }

     connection(pseudo){
          var __data__ = {
               "pseudo": pseudo,
               "etat": "Actif"
          }
          updateUser(pseudo, __data__)
          .then(
               res=>{
                    toast.success("Connexion réussie")
                    console.log(res)
               }
          )
          .catch(
               error=>{
                    toast.error("Une erreur est survenue lors de la déconnexion")
                    console.log(error)
               }
          )
     }

     render() {
          // formulaire
          const { emailCorrect, passwordCorrect, connexionToast, isRedirected, wantToSignIn, users } = this.state
          return (
               <>
               {!isRedirected && !wantToSignIn && (
               <div className="login-form">
                    
                    <div>
                         {connexionToast}
                         <h2><img src={openprofLogo} alt="" width="450" height="300"/></h2>
                         {/* <h3>Web Version v1.0</h3> */}
                         <form action="/">
                              <div className="form-group">
                                   <p><input type="text" className="input-cst" placeholder="Adresse e-mail" name="login_email_user" onChange={this.emailListener}></input></p>
                                   <p><input type="password" className="input-cst" name="login_password_user" placeholder="Mot de passe" onChange={this.passwordListener}></input></p>
                              </div>
                              <button className="btn-cst" id="connect_account" disabled={(emailCorrect && passwordCorrect) ? false : true} onClick={this.onSubmit}><FontAwesomeIcon icon={["fas", "fa-hand-point-right"]} /> Connexion</button> <br/>
                              {/* <hr/> */}
                              {/* <button className="btn-cst" id="create_account" onClick={()=>this.setState({wantToSignIn: true})}>Créer un compte</button> */}
                         </form>
                    </div>
                    {/* <div class="dashboard">
                         <ul class="list-cst">
                              {users ? users.map(user=>{
                                   <li>
                                        <span>
                                             {user.etat}
                                        </span>
                                        <div>
                                             <span>{user.pseudo}</span>
                                        </div>
                                   </li>
                              }) : (
                                   <li>Aucun résultat...</li>
                              )}
                         </ul>
                    </div> */}
                    

               </div>
               )}
               {isRedirected && !wantToSignIn && (
                    <Home account={this.state.accountUserIfValid} isConnected={this.state.isRedirected} auth={[this.formLogin.email, this.formLogin.password]}/>
               )}
               {!isRedirected && wantToSignIn && (
                   <Signin /> 
               )}
          </>
          )}
}

export default Login;

